<template><undefined>

<h1>Slider 滑动输入条</h1>
<p>滑动型输入器，展示当前值和可选范围。</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<p>当用户需要在数值区间/自定义区间内进行选择时，可为连续或离散值。</p>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;div&gt;\n        &lt;v-slider v-model=\&quot;sliderValue\&quot; :disabled=\&quot;disabled\&quot;&gt;&lt;/v-slider&gt;\n        &lt;v-slider range v-model=\&quot;sliderRange\&quot; :disabled=\&quot;disabled\&quot;&gt;&lt;/v-slider&gt;\n        &lt;div&gt;\n            Disabled: &lt;v-switch v-model=\&quot;disabled\&quot;&gt;&lt;/v-switch&gt;\n        &lt;/div&gt;\n    &lt;/div&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n    export default {\n        data() {\n            return {\n                disabled: false,\n                sliderValue: 30,\n                sliderRange: [20, 50],\n            };\n        },\n    };\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <div>
        <v-slider v-model="sliderValue" :disabled="disabled"></v-slider>
        <v-slider range="" v-model="sliderRange" :disabled="disabled"></v-slider>
        <div>
            Disabled: <v-switch v-model="disabled"></v-switch>
        </div>
    </div>
</template>


</div>
                  <div slot="description"><h4 id="ji-ben"><a class="header-anchor" href="#ji-ben" aria-hidden="true">¶</a> 基本</h4>
<p>基本滑动条。当 <code>range</code> 为 <code>true</code> 时，渲染为双滑块。当 <code>disabled</code> 为 <code>true</code> 时，滑块处于不可用状态。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-slider</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"sliderValue"</span> <span class="hljs-attr">:disabled</span>=<span class="hljs-string">"disabled"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-slider</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-slider</span> <span class="hljs-attr">range</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"sliderRange"</span> <span class="hljs-attr">:disabled</span>=<span class="hljs-string">"disabled"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-slider</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
            Disabled: <span class="hljs-tag">&lt;<span class="hljs-name">v-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"disabled"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-switch</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data() {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">disabled</span>: <span class="hljs-literal">false</span>,
                <span class="hljs-attr">sliderValue</span>: <span class="hljs-number">30</span>,
                <span class="hljs-attr">sliderRange</span>: [<span class="hljs-number">20</span>, <span class="hljs-number">50</span>],
            };
        },
    };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
