<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;div class=\&quot;icon-wrapper\&quot;&gt;\n        &lt;v-icon type=\&quot;frown-o\&quot; :class=\&quot;customIconValue &lt;= 50 ? 'anticon-highlight' : ''\&quot;&gt;&lt;/v-icon&gt;\n        &lt;v-slider v-model=\&quot;customIconValue\&quot;&gt;&lt;/v-slider&gt;\n        &lt;v-icon type=\&quot;smile-o\&quot; :class=\&quot;customIconValue &gt; 50 ? 'anticon-highlight' : ''\&quot;&gt;&lt;/v-icon&gt;\n    &lt;/div&gt;\n&lt;/template&gt;\n\n\n\n\n&quot;,&quot;script&quot;:&quot;\n    export default {\n        data() {\n            return {\n                customIconValue: 30,\n            };\n        },\n    };\n&quot;,&quot;style&quot;:&quot;\n    .icon-wrapper {\n        position: relative;\n        padding: 0px 30px;\n    }\n    \n    .icon-wrapper .anticon {\n        position: absolute;\n        top: -3px;\n        width: 16px;\n        height: 16px;\n        line-height: 1;\n        font-size: 16px;\n        color: rgba(0, 0, 0, 0.3);\n    }\n    \n    .icon-wrapper .anticon:first-child {\n        left: 0;\n    }\n    \n    .icon-wrapper .anticon:last-child {\n        right: 0;\n    }\n    \n    .anticon.anticon-highlight {\n        color: rgba(0, 0, 0, 0.6);\n    }\n&quot;}">
                  <div class="box-demo-show" slot="component"><template>
    <div class="icon-wrapper">
        <v-icon type="frown-o" :class="customIconValue <= 50 ? 'anticon-highlight' : ''"></v-icon>
        <v-slider v-model="customIconValue"></v-slider>
        <v-icon type="smile-o" :class="customIconValue > 50 ? 'anticon-highlight' : ''"></v-icon>
    </div>
</template>




</div>
                  <div slot="description"><h4 id="dai-icon-de-hua-kuai"><a class="header-anchor" href="#dai-icon-de-hua-kuai" aria-hidden="true">¶</a> 带 icon 的滑块</h4>
