        <span class="hljs-tag">&lt;<span class="hljs-name">v-icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"frown-o"</span> <span class="hljs-attr">:class</span>=<span class="hljs-string">"customIconValue &lt;= 50 ? 'anticon-highlight' : ''"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-icon</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-slider</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"customIconValue"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-slider</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"smile-o"</span> <span class="hljs-attr">:class</span>=<span class="hljs-string">"customIconValue &gt; 50 ? 'anticon-highlight' : ''"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-icon</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data() {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">customIconValue</span>: <span class="hljs-number">30</span>,
            };
        },
    };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">style</span>&gt;</span><span class="css">
    <span class="hljs-selector-class">.icon-wrapper</span> {
        <span class="hljs-attribute">position</span>: relative;
