    }
    
    <span class="hljs-selector-class">.icon-wrapper</span> <span class="hljs-selector-class">.anticon</span> {
        <span class="hljs-attribute">position</span>: absolute;
        <span class="hljs-attribute">top</span>: -<span class="hljs-number">3px</span>;
        <span class="hljs-attribute">width</span>: <span class="hljs-number">16px</span>;
        <span class="hljs-attribute">height</span>: <span class="hljs-number">16px</span>;
        <span class="hljs-attribute">line-height</span>: <span class="hljs-number">1</span>;
        <span class="hljs-attribute">font-size</span>: <span class="hljs-number">16px</span>;
        <span class="hljs-attribute">color</span>: <span class="hljs-built_in">rgba</span>(0, 0, 0, 0.3);
    }
    
    <span class="hljs-selector-class">.icon-wrapper</span> <span class="hljs-selector-class">.anticon</span><span class="hljs-selector-pseudo">:first-child</span> {
        <span class="hljs-attribute">left</span>: <span class="hljs-number">0</span>;
    }
    
    <span class="hljs-selector-class">.icon-wrapper</span> <span class="hljs-selector-class">.anticon</span><span class="hljs-selector-pseudo">:last-child</span> {
        <span class="hljs-attribute">right</span>: <span class="hljs-number">0</span>;
    }
    
    <span class="hljs-selector-class">.anticon</span><span class="hljs-selector-class">.anticon-highlight</span> {
        <span class="hljs-attribute">color</span>: <span class="hljs-built_in">rgba</span>(0, 0, 0, 0.6);
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>
</code></pre>
</div>
