<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;div&gt;\n        &lt;v-slider v-model=\&quot;sliderValue\&quot; @before-change=\&quot;beforeChange\&quot; @change=\&quot;onChange\&quot; @after-change=\&quot;afterChange\&quot;&gt;&lt;/v-slider&gt;\n        &lt;v-slider range v-model=\&quot;sliderRange\&quot; @before-change=\&quot;beforeChange\&quot; @change=\&quot;onChange\&quot; @after-change=\&quot;afterChange\&quot;&gt;&lt;/v-slider&gt;\n    &lt;/div&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n    export default {\n        data() {\n            return {\n                sliderValue: 30,\n                sliderRange: [20, 50],\n            };\n        },\n        methods: {\n            beforeChange(value) {\n                console.log(`before slider change: ${value}`);\n            },\n            onChange(value) {\n                console.log(`slider changed: ${value}`);\n            },\n            afterChange(value) {\n                console.log(`after slider change: ${value}`);\n            },\n        },\n    };\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <div>
        <v-slider v-model="sliderValue" @before-change="beforeChange" @change="onChange" @after-change="afterChange"></v-slider>
        <v-slider range="" v-model="sliderRange" @before-change="beforeChange" @change="onChange" @after-change="afterChange"></v-slider>
    </div>
</template>


</div>
                  <div slot="description"><h4 id="shi-jian"><a class="header-anchor" href="#shi-jian" aria-hidden="true">¶</a> 事件</h4>
<p>当 Slider 的值发生改变时，会触发 <code>change</code> 事件，并把改变后的值作为参数传入。在 <code>onmouseup</code> 时，会触发 <code>afterChange</code> 事件，并把当前值作为参数传入。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-slider</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"sliderValue"</span>  <span class="hljs-attr">@before-change</span>=<span class="hljs-string">"beforeChange"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"onChange"</span> <span class="hljs-attr">@after-change</span>=<span class="hljs-string">"afterChange"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-slider</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-slider</span> <span class="hljs-attr">range</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"sliderRange"</span>  <span class="hljs-attr">@before-change</span>=<span class="hljs-string">"beforeChange"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"onChange"</span> <span class="hljs-attr">@after-change</span>=<span class="hljs-string">"afterChange"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-slider</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data() {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">sliderValue</span>: <span class="hljs-number">30</span>,
                <span class="hljs-attr">sliderRange</span>: [<span class="hljs-number">20</span>, <span class="hljs-number">50</span>],
            };
        },
        <span class="hljs-attr">methods</span>: {
            beforeChange(value) {
                <span class="hljs-built_in">console</span>.log(<span class="hljs-string">`before slider change: <span class="hljs-subst">${value}</span>`</span>);
            },
            onChange(value) {
                <span class="hljs-built_in">console</span>.log(<span class="hljs-string">`slider changed: <span class="hljs-subst">${value}</span>`</span>);
            },
            afterChange(value) {
                <span class="hljs-built_in">console</span>.log(<span class="hljs-string">`after slider change: <span class="hljs-subst">${value}</span>`</span>);
            },
        },
    };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
