                  <div slot="description"><h4 id="zi-ding-yi-ti-shi"><a class="header-anchor" href="#zi-ding-yi-ti-shi" aria-hidden="true">¶</a> 自定义提示</h4>
<p>使用 <code>tipFormatter</code> 可以格式化 <code>Tooltip</code> 的内容，设置 <code>tipFormatter={null}</code>，则隐藏 <code>Tooltip</code>。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-slider</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"30"</span> <span class="hljs-attr">:tip-formatter</span>=<span class="hljs-string">"tipFormatter"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-slider</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-slider</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"30"</span> <span class="hljs-attr">:tip-formatter</span>=<span class="hljs-string">"null"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-slider</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data() {
            <span class="hljs-keyword">return</span> {};
        },
        <span class="hljs-attr">methods</span>: {
            tipFormatter(value) {
                <span class="hljs-keyword">return</span> <span class="hljs-string">`<span class="hljs-subst">${value}</span>%`</span>;
            },
        },
    };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
