                  <div class="box-demo-show" slot="component"><template>
    <div>
        <h4>included = true</h4>
        <v-slider :value="37" :marks="marks"></v-slider>
        <v-slider :value="[26, 37]" range="" :marks="marks"></v-slider>

        <h4>included = false</h4>
        <v-slider :value="37" :marks="marks" :included="false"></v-slider>

        <h4>marks & step</h4>
        <v-slider :value="37" :marks="marks" :step="10" dots=""></v-slider>

        <h4>step = null</h4>
        <v-slider :value="37" :marks="marks" :step="null"></v-slider>
    </div>
</template>
</div>
                  <div slot="description"><h4 id="dai-biao-qian-de-hua-kuai"><a class="header-anchor" href="#dai-biao-qian-de-hua-kuai" aria-hidden="true">¶</a> 带标签的滑块</h4>
<p>使用 <code>marks</code> 属性标注分段式滑块，使用 <code>value</code> 指定滑块位置。当 <code>included=false</code> 时，表明不同标记间为并列关系。当 <code>step=null</code> 时，Slider 的可选值仅有 <code>marks</code> 标出来的部分。</p>
</div>
