        <span class="hljs-tag">&lt;<span class="hljs-name">v-slider</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"37"</span> <span class="hljs-attr">:marks</span>=<span class="hljs-string">"marks"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-slider</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-slider</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"[26, 37]"</span> <span class="hljs-attr">range</span> <span class="hljs-attr">:marks</span>=<span class="hljs-string">"marks"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-slider</span>&gt;</span>

        <span class="hljs-tag">&lt;<span class="hljs-name">h4</span>&gt;</span>included = false<span class="hljs-tag">&lt;/<span class="hljs-name">h4</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-slider</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"37"</span> <span class="hljs-attr">:marks</span>=<span class="hljs-string">"marks"</span> <span class="hljs-attr">:included</span>=<span class="hljs-string">"false"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-slider</span>&gt;</span>

        <span class="hljs-tag">&lt;<span class="hljs-name">h4</span>&gt;</span>marks &amp; step<span class="hljs-tag">&lt;/<span class="hljs-name">h4</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-slider</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"37"</span> <span class="hljs-attr">:marks</span>=<span class="hljs-string">"marks"</span> <span class="hljs-attr">:step</span>=<span class="hljs-string">"10"</span> <span class="hljs-attr">dots</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-slider</span>&gt;</span>

        <span class="hljs-tag">&lt;<span class="hljs-name">h4</span>&gt;</span>step = null<span class="hljs-tag">&lt;/<span class="hljs-name">h4</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-slider</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"37"</span> <span class="hljs-attr">:marks</span>=<span class="hljs-string">"marks"</span> <span class="hljs-attr">:step</span>=<span class="hljs-string">"null"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-slider</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
</code></pre>
</div>
