        <span class="hljs-tag">&lt;<span class="hljs-name">v-slider</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"37"</span> <span class="hljs-attr">:marks</span>=<span class="hljs-string">"marks"</span> <span class="hljs-attr">:step</span>=<span class="hljs-string">"10"</span> <span class="hljs-attr">dots</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-slider</span>&gt;</span>

        <span class="hljs-tag">&lt;<span class="hljs-name">h4</span>&gt;</span>step = null<span class="hljs-tag">&lt;/<span class="hljs-name">h4</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-slider</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"37"</span> <span class="hljs-attr">:marks</span>=<span class="hljs-string">"marks"</span> <span class="hljs-attr">:step</span>=<span class="hljs-string">"null"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-slider</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
</code></pre>
</demo-box>
<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="slider-props"><a class="header-anchor" href="#slider-props" aria-hidden="true">¶</a> Slider Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>range</td>
<td>双滑块模式</td>
<td>boolean</td>
<td>false</td>
</tr>
<tr>
<td>min</td>
<td>最小值</td>
<td>number</td>
<td>0</td>
</tr>
<tr>
<td>max</td>
<td>最大值</td>
<td>number</td>
<td>100</td>
</tr>
<tr>
<td>step</td>
<td>步长，取值必须大于 0，并且可被 (max - min) 整除。当 <code v-pre="">marks</code> 不为空对象时，可以设置 <code v-pre="">step</code> 为 <code v-pre="">null</code>，此时 Slider 的可选值仅有 marks 标出来的部分。</td>
<td>number|null</td>
<td>1</td>
</tr>
<tr>
<td>marks</td>
<td>刻度标记，key 的类型必须为 <code v-pre="">number</code> 且取值在闭区间 [min, max] 内</td>
<td>object</td>
<td>{ number: string }</td>
</tr>
<tr>
<td>dots</td>
<td>显示 <code v-pre="">step</code> 间断点，建议在 <code v-pre="">step</code> 间隔不密集时使用</td>
<td>boolean</td>
<td>false</td>
</tr>
<tr>
<td>value</td>
<td>设置当前取值。当 <code v-pre="">range</code> 为 <code v-pre="">false</code> 时，使用 <code v-pre="">number</code>，否则用 <code v-pre="">[number, number]</code></td>
<td>number|number[]</td>
<td></td>
</tr>
<tr>
<td>included</td>
<td><code v-pre="">marks</code> 不为空对象时有效，值为 true 时表示值为包含关系，false 表示并列</td>
<td>boolean</td>
<td>true</td>
</tr>
<tr>
<td>disabled</td>
<td>值为 <code v-pre="">true</code> 时，滑块为禁用状态</td>
<td>boolean</td>
<td>false</td>
</tr>
<tr>
<td>tip-formatter</td>
<td>Slider 会把当前值传给 <code v-pre="">tip-formatter</code>，并在 Tooltip 中显示 <code v-pre="">tip-formatter</code> 的返回值，若为 null，则隐藏 Tooltip。</td>
<td>Function|null</td>
<td>IDENTITY</td>
</tr>
</tbody>
</table>
<h3 id="slider-events"><a class="header-anchor" href="#slider-events" aria-hidden="true">¶</a> Slider Events</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>before-change</td>
<td>当 Slider 的值发生改变前触发的事件</td>
<td>value</td>
</tr>
<tr>
<td>change</td>
<td>当 Slider 的值发生改变时触发的事件</td>
<td>value</td>
</tr>
<tr>
<td>after-change</td>
<td>与 <code v-pre="">onmouseup</code> 触发时机一致</td>
<td>value</td>
</tr>
</tbody>
</table>
</undefined></template>
<style>
    .icon-wrapper {
        position: relative;
        padding: 0px 30px;
    }
    
    .icon-wrapper .anticon {
        position: absolute;
        top: -3px;
        width: 16px;
        height: 16px;
        line-height: 1;
        font-size: 16px;
        color: rgba(0, 0, 0, 0.3);
    }
    
    .icon-wrapper .anticon:first-child {
        left: 0;
    }
    
    .icon-wrapper .anticon:last-child {
        right: 0;
    }
    
    .anticon.anticon-highlight {
        color: rgba(0, 0, 0, 0.6);
    }
</style>
<script>
    export default {
        data() {
            return {
                disabled: false,
                sliderValue: 30,
                sliderRange: [20, 50],
                customIconValue: 30,
                synValue: 2,
                synFloatValue: 0.2,
                marks: {
                    0: '0°C',
                    26: '26°C',
                    37: '37°C',
                    100: '100°C',
                },
            };
        },
        methods: {
            customIconChange(value) {
                
            },
            tipFormatter(value) {
                return `${value}%`;
            },
            beforeChange(value) {
                console.log(`before slider change: ${value}`);
            },
            onChange(value) {
                console.log(`slider changed: ${value}`);
            },
            afterChange(value) {
                console.log(`after slider change: ${value}`);
            },
        },
    };
</script>


